.page-template-template-portfolio .portfolio {
  margin: 60px 0;
  padding: 0 60px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 60px;
}
@media (min-width: 1441px) {
  .page-template-template-portfolio .portfolio {
    padding: 0 0;
  }
}
.page-template-template-portfolio .portfolio__filter {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.page-template-template-portfolio .portfolio__categories {
  width: 100%;
  padding-bottom: 20px;
  border-bottom: 1px solid #dddddd;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}
.page-template-template-portfolio .portfolio__categories .category {
  padding: 10px 5px;
  font-size: 2rem;
  font-weight: 600;
  text-transform: capitalize;
  letter-spacing: 1px;
  color: #616263;
  cursor: pointer;
  transition: 0.3s linear;
}
@media (min-width: 768px) {
  .page-template-template-portfolio .portfolio__categories .category {
    font-size: 20px;
  }
}
.page-template-template-portfolio .portfolio__categories .category.selected, .page-template-template-portfolio .portfolio__categories .category:hover {
  color: rgb(var(--secondary-color));
}
.page-template-template-portfolio .portfolio__inner {
  width: 100%;
}
.page-template-template-portfolio .portfolio__inner .projects {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 30px;
}
@media (min-width: 768px) {
  .page-template-template-portfolio .portfolio__inner .projects {
    flex-flow: row wrap;
  }
}
.page-template-template-portfolio .portfolio__inner .projects .project {
  width: 100%;
  height: 232px;
  position: relative;
  display: flex;
  z-index: -15;
  overflow: hidden;
  opacity: 0;
  transform: scale(0);
  visibility: hidden;
  transition: transform 0.2s linear, opacity 0.2s linear 0.1s, z-index 0.2s linear 0.2s;
}
@media (min-width: 768px) {
  .page-template-template-portfolio .portfolio__inner .projects .project {
    height: 260px;
    min-height: fit-content;
    flex: 0 0 calc(50% - 30px);
  }
}
@media (min-width: 1441px) {
  .page-template-template-portfolio .portfolio__inner .projects .project {
    height: 362px;
    flex: 0 0 calc(33.333% - 30px);
  }
}
.page-template-template-portfolio .portfolio__inner .projects .project.visible {
  opacity: 1;
  transform: scale(1);
  z-index: 1;
  visibility: visible;
}
.page-template-template-portfolio .portfolio__inner .projects .project:hover .bg-img {
  transform: scale(1.2);
}
.page-template-template-portfolio .portfolio__inner .projects .project:hover .project-wrapper {
  height: 100%;
  top: 0;
  background-color: rgba(var(--secondary-color), 0.8);
  opacity: 1;
}
.page-template-template-portfolio .portfolio__inner .projects .project:hover .content-wrapper {
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.page-template-template-portfolio .portfolio__inner .projects .project:hover .title {
  bottom: -100px;
  opacity: 0;
}
.page-template-template-portfolio .portfolio__inner .projects .project .bg-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  object-fit: cover;
  object-position: center center;
  z-index: 0;
  transition: 0.3s linear;
}
.page-template-template-portfolio .portfolio__inner .projects .project-wrapper {
  width: 100%;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  opacity: 0;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s ease 0.15s;
}
@media (min-width: 1441px) {
  .page-template-template-portfolio .portfolio__inner .projects .project-wrapper {
    padding: 70px 40px;
  }
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper {
  width: 100%;
  margin: 0;
  padding: 15px 10px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease 0s;
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper .heading {
  padding-bottom: 30px;
  margin-bottom: 10px;
  position: relative;
  font-size: 24px;
  line-height: normal;
  font-weight: 500;
  color: #fff;
  text-align: center;
  transition: 0.3s linear;
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper .heading:hover {
  color: rgb(var(--primary-color));
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper .heading:hover::after {
  width: 60px;
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper .heading::after {
  content: "";
  position: absolute;
  width: 40px;
  height: 2px;
  background: #ffffff;
  bottom: 13px;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.3s linear;
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper .buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper .icon-button {
  height: 45px;
  width: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  transition: transform 0.5s, background-color 0.5s;
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper .icon-button:hover {
  background-color: rgb(var(--primary-color));
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper .icon-button:hover svg {
  fill: #fff;
}
.page-template-template-portfolio .portfolio__inner .projects .project .content-wrapper .icon-button svg {
  width: 16px;
  height: 16px;
  fill: rgb(var(--primary-color));
}
.page-template-template-portfolio .portfolio__inner .projects .project .title {
  width: 100%;
  padding: 15px 30px;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 1;
  background-color: rgb(var(--primary-color));
  text-align: center;
  transition: all 0.3s ease 0s;
}
.page-template-template-portfolio .portfolio__inner .projects .project .title a {
  font-size: 20px;
  font-weight: 500;
  color: #fff;
}
.page-template-template-portfolio #gallery-overlay {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  visibility: visible;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.85);
  z-index: 1100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.4s ease-in-out;
}
.page-template-template-portfolio #gallery-overlay .spinner {
  width: 150px;
  height: 150px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  opacity: 1;
  display: none;
}
.page-template-template-portfolio #gallery-overlay .gallery-slide {
  max-height: 100%;
  height: 100%;
  min-width: 280px;
  flex: 1 0 90%;
}
.page-template-template-portfolio #gallery-overlay .gallery-slide__inner {
  max-width: 1165px;
  width: 90%;
  height: 100%;
  margin: 0 auto;
  overflow-y: scroll;
  scrollbar-width: none;
  scroll-behavior: smooth;
  display: block;
}
.page-template-template-portfolio #gallery-overlay .gallery-slide__inner img {
  height: 80vh;
  width: 100%;
  margin: 75px 0;
  display: block;
  object-fit: contain;
  object-position: center center;
  transition: 0.3s linear;
}
.page-template-template-portfolio #gallery-overlay .gallery-selector {
  max-height: 100%;
  height: 100%;
  flex: 0 0 100%;
  display: flex;
  align-items: center;
}
@media (max-width: 1024px) {
  .page-template-template-portfolio #gallery-overlay .gallery-selector {
    display: none;
  }
}
.page-template-template-portfolio #gallery-overlay .gallery-selector__inner {
  min-height: fit-content;
  margin: 0 30px;
  overflow-y: scroll;
  scrollbar-width: none;
  scroll-behavior: smooth;
  display: block;
}
.page-template-template-portfolio #gallery-overlay .gallery-selector__inner a {
  height: 100px;
  width: 100px;
  margin: 17.5px;
  display: flex;
  transition: 0.3s linear 0.5s;
}
.page-template-template-portfolio #gallery-overlay .gallery-selector__inner img {
  height: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
}
.page-template-template-portfolio #gallery-overlay .gallery-overlay-close {
  width: 75px;
  height: 75px;
  position: fixed;
  right: 20px;
  top: 20px;
  display: block;
  border-radius: 50%;
  opacity: 1;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.page-template-template-portfolio #gallery-overlay .gallery-overlay-close.open {
  opacity: 1;
}
.page-template-template-portfolio #gallery-overlay .gallery-overlay-close div {
  width: 50px;
  height: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: #fff;
  transition: all 0.3s linear;
}
.page-template-template-portfolio #gallery-overlay .gallery-overlay-close div:first-child {
  transform: translate(-50%, -50%) rotate(45deg);
}
.page-template-template-portfolio #gallery-overlay .gallery-overlay-close div:last-child {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.page-template-template-portfolio #gallery-overlay .gallery-overlay-close:hover div:first-child {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.page-template-template-portfolio #gallery-overlay .gallery-overlay-close:hover div:last-child {
  transform: translate(-50%, -50%) rotate(45deg);
}